@import url(https://fonts.googleapis.com/css?family=Montserrat:400,600,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  box-sizing: border-box; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

html {
  font-family: 'Montserrat', sans-serif; }

.App {
  width: 100vw;
  display: flex; }

.side-nav {
  height: 100%;
  position: absolute;
  width: 25%;
  background: #11112F;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 65px 0;
  overflow: scroll; }
  .side-nav::-webkit-scrollbar {
    width: 0 !important; }
  .side-nav #logo {
    width: 150px;
    margin: 35px 0; }
  .side-nav .side-nav-items {
    margin: 2%;
    color: white;
    font-size: 22px; }
    .side-nav .side-nav-items li {
      font-weight: 700;
      margin: 60px 0; }

.main-content {
  height: 100%;
  width: 75%;
  position: absolute;
  right: 0;
  background: #e3ebff;
  padding: 28px 80px;
  overflow: scroll; }
  .main-content::-webkit-scrollbar {
    width: 0 !important; }
  .main-content #race-car {
    position: fixed;
    left: 25vw;
    top: 10vh;
    width: 100px;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg); }
  .main-content #heading {
    font-size: 44px;
    font-weight: 700;
    padding: 44px 0px; }
  .main-content .info-box {
    background: #FFFFFF;
    width: 100%;
    padding: 44px 70px; }
    .main-content .info-box h2 {
      color: #EB108B;
      font-weight: bold;
      font-size: 28px;
      margin-bottom: 40px; }
    .main-content .info-box .info {
      display: flex; }
      .main-content .info-box .info div {
        height: 112px;
        padding: 28px 20px;
        font-weight: 600;
        width: 33.33%; }
      .main-content .info-box .info .time {
        font-size: 14px;
        border-right: solid #EB108B 1px; }
      .main-content .info-box .info .title-location {
        font-size: 18px;
        width: 33.33%; }
        .main-content .info-box .info .title-location #location {
          margin-top: 4px;
          font-weight: normal;
          font-size: 80%; }

